.form {
  width: 327px;
  position: absolute;
  left: 59.6%;
  top: -503px;

  @media (max-width: $tablet-width) {
    width: 321px;
    position: static;
  }

  @media (max-width: $mobile-width) {
    width: 288px;
  }
}

.form p {
  @include text-reset;
  font-weight: 600;
  font-size: 17px;
  line-height: calc(28 / 17);
  letter-spacing: -0.1px;
  color: $light-1;
  text-align: center;
  margin-bottom: 20px;

  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    line-height: 1.65;
  }

  @media (max-width: $tablet-width) {
    font-size: 18px;
    line-height: calc(28 / 18);
    color: $dark-5;
    margin-bottom: 30px;

    @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
      line-height: 1.56;
    }
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 20px;
  }
}

.form input {
  width: 327px;
  height: 48px;
  border: solid 1px $light-3;
  border-radius: 4px;
  background-color: $light-1;
  font-family: "Open Sans", "Tahoma", sans-serif;
  font-size: 15px;
  line-height: calc(24 / 15);
  color: $dark-5;
  padding: 12px 29px;
  margin-bottom: 20px;

  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    line-height: 1.6;
  }

  @media (max-width: $tablet-width) {
    width: 321px;
    padding: 12px 21px;
  }

  @media (max-width: $mobile-width) {
    width: 288px;
    padding: 12px 26px;
  }
}

.form input::placeholder {
  font-family: "Open Sans", "Tahoma", sans-serif;
  font-size: 15px;
  line-height: calc(24 / 15);
  color: rgba($dark-3, 0.5);

  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    line-height: 1.6;
  }
}

.form input:hover,
.form input:focus {
  background-color: $form-hover;
  box-shadow: 0 0 0 3px $basic;
  border: transparent;
  cursor: pointer;
  transition: all 0.2s ease;
}

.form input:invalid:not(:placeholder-shown) {
  box-shadow: 0 0 0 2px $form-invalid;
  border: transparent;

  @media (max-width: $tablet-width) {
    box-shadow: 0 0 0 1px $form-invalid;
    border: transparent;
  }
}

.form button {
  font-family: "Open Sans", "Tahoma", sans-serif;
  font-weight: 600;
  font-size: 15px;
  line-height: calc(21 / 15);
  color: $dark-5;
  text-align: center;

  width: 327px;
  height: 51px;
  border: none;
  border-radius: 4px;
  background-color: $basic;

  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    line-height: 1.4;
  }

  @media (max-width: $tablet-width) {
    width: 321px;
  }

  @media (max-width: $mobile-width) {
    width: 288px;
  }
}

.form button:hover,
.form button:focus {
  background-color: $basic-hover;
  cursor: pointer;
  transition: all 0.2s ease;
}

.form button:active {
  background-color: rgba($basic-hover, 0.4);
  transition: all 0.2s ease;
}
